import React, { Component, getGlobal } from "reactn";
import { Helmet } from "react-helmet";
// import { Route, Redirect, HashRouter, BrowserRouter as Router } from 'react-router-dom';
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Pagination from "react-js-pagination";
import { authGet, authPatch, deleteAuth, postAuth, axiosPatchFormData } from "../../../util/Service";
import { HOC } from "../../../util/HOC";
import CircularProgress from "@material-ui/core/CircularProgress/CircularProgress";

type props = {
  history: any;
  showAlert: any;
};
type states = {
  toDay: any;
  isAddModalopen: boolean;
  isDeleteModalOpen: boolean;
  isEditModalopen: boolean;
  isFaceImgOpen: boolean;
  employees: any;
  editEmp: any;
  addEmp: any;
  validation: any;
  isValidate: Boolean;
  deleteEmpId: string;
  deleteEmpName: string;
  isAddEditLoading: boolean;
  search: any;
  isLoading: boolean;
  itemsCountPerPage: number;
  currentPage: number;
  prifilePhoto: any;
};
class Employee extends Component<props, states> {
  constructor() {
    super();
    this.state = {
      currentPage: 1,
      itemsCountPerPage: 5,
      toDay: new Date(),
      isAddModalopen: false,
      isDeleteModalOpen: false,
      isEditModalopen: false,
      isFaceImgOpen: false,
      isAddEditLoading: false,
      isLoading: false,
      search: null,
      deleteEmpId: '',
      deleteEmpName: '',
      prifilePhoto: '',
      addEmp: {
        employee_id: "",
        name: "",
        email: "",
        password: "",
        phone: "",
        designation: "",
        department: "",
        gender: "Male",
        dob: new Date().toISOString(),
        company_id: "",
        is_corona_positive: false,
        corona_positive_date: new Date().toISOString()
      },
      isValidate: false,
      validation: {
        employee_id: "",
        name: "",
        email: "",
        password: "",
        phone: ""
      },
      editEmp: {
      },
      employees: [
      ],
    };
  }
  componentDidMount() {
    // this._getEmployee()
    this.setState({ isLoading: true }, () => this._getEmployee())
  }

  resetAddEmp = () => {
    this.setState({
      addEmp: {
        employee_id: "",
        name: "",
        email: "",
        password: "",
        phone: "",
        designation: "",
        department: "",
        gender: "Male",
        dob: new Date().toISOString(),
        company_id: "",
        is_corona_positive: false,
        corona_positive_date: new Date().toISOString()
      }
    })
  }

  _getEmployee = () => {
    // let { company } = getGlobal();
    // let key = company.unique_id;
    authGet("/api/employees")
      .then((res) => {
        console.log(res, '====>')
        // return
        const status = res.request.status;
        if (status === 201) {
          if (res.data.data.length > 0) {
            this.setState({
              isLoading: false,
              employees: res.data.data
            });
          }
          else {
            this.toggleModal('ADD')
          }
        } else {
          this.setState({
            isLoading: false
          });
        }
      })
      .catch((error) => {
        console.log(error);
        this.setState({
          isLoading: false
        });
      });
  }
  /**
     * @description handle date on change
     * @param {Date} date
     */
  onChangeDate = (date: any) => {
    let event = new Date(date);
    // console.log('jhvjh',event.toISOString());
    let addEmp = { ...this.state.addEmp }
    addEmp['dob'] = event.toISOString();
    this.setState({
      addEmp,
      toDay: event
    });
  }

  onChangeDateEditDOB = (date: any) => {
    let event = new Date(date);
    // console.log('jhvjh',event.toISOString());
    let editEmp = { ...this.state.editEmp }
    editEmp['dob'] = event.toISOString();
    this.setState({
      editEmp
    });
  }


  /**
   * @description handle date on change
   * @param {Date} date
   */
  onChangeCoronaDate = (date: any) => {
    let event = new Date(date);
    let addEmp = { ...this.state.addEmp };
    addEmp.corona_positive_date = event.toISOString();

    this.setState({
      addEmp
    });
  }

  /**
   * @description handle date on change
   * @param {Date} date
   */
  onChangeCoronaDateEdit = (date: any) => {
    let event = new Date(date);
    let editEmp = { ...this.state.editEmp };
    editEmp.corona_positive_date = event.toISOString();
    this.setState({
      editEmp
    });
  }

  /**
     * @description handle toggle popup
     * @param {string} name
     */
  toggleModal = (name: string) => {
    switch (name) {
      case "ADD":
        this.resetAddEmp();
        this.setState({
          isAddModalopen: !this.state.isAddModalopen
        });
        break;
      case "DELETE":
        this.setState({
          isDeleteModalOpen: !this.state.isDeleteModalOpen,
        });
        break;
      case "EDIT":
        this.setState({
          isEditModalopen: !this.state.isEditModalopen,
        });
        break;
      case "FACE":
        this.setState({
          isFaceImgOpen: !this.state.isFaceImgOpen,
        });
    }
  }

  /**
     * @description handle add popup input
     * @param {string} event/data
     * @param {string} name
     */
  handleAddChange = (event: any, name: string) => {
    let addEmp = { ...this.state.addEmp };
    if (name === 'is_corona_positive') {
      if (event.target.value === 'positive') {
        addEmp[name] = true;
      } else {
        addEmp[name] = false;
      }
    } else {
      this.validateInput(event, name);
      addEmp[name] = event.target.value;
    }

    this.setState({ addEmp });
    // console.log(this.state)
  }

  /**
   * @description handle edit popup input
   * @param {string} event/data
   * @param {string} name
   */
  handleEditChange = (event: any, name: string) => {
    let editEmp = { ...this.state.editEmp };
    if (name === 'is_corona_positive') {
      if (event.target.value === 'positive') {
        editEmp[name] = true;
      } else {
        editEmp[name] = false;
      }
    } else {
      this.validateInput(event, name);
      editEmp[name] = event.target.value;
    }

    this.setState({ editEmp });
  }

  validateInput = (event: any, name: string) => {
    const value = event.target.value;
    let validation = { ...this.state.validation };

    switch (name) {
      case 'employee_id':
        if (value === '') {
          validation[name] = 'Please enter employee id';
        }
        else {
          validation[name] = '';
        }
        break;

      case 'name':
        if (value === '') {
          validation[name] = 'Please enter employee name';
        }
        else {
          let regx = /^[a-zA-Z]+$/
          if (regx.test(value)) {
            validation[name] = '';
          }
          else {
            validation[name] = 'Only alphabet accepted.';
          }
        }
        break;
      case 'password':
        if (value === '') {
          validation[name] = 'Please enter initial password';
        }
        else {
          validation[name] = '';
        }
        break;
      case 'phone':
        if (value === '') {
          validation[name] = 'Please enter phone number';
        }
        else {
          let reg = /^[0-9]+$/;
          if (reg.test(value)) {
            validation[name] = '';
          } else {
            validation[name] = 'Only numbers accepted.';
          }

        }
        break;
      case 'email':
        if (value === '') {
          validation[name] = 'Please enter email address';
        }
        else {
          // console.log('kjbkj')
          const reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/
          if (reg.test(value)) {
            validation[name] = '';
          } else {
            validation[name] = 'Please enter valid email address';
          }
        }
        break;

      default:
        console.log('Default')
        break;
    }
    this.setState({ validation })
  }



  /**
   * @description add employee http call
   */
  _submitAdd = (e: any) => {

    let data = { ...this.state.addEmp };
    let { company } = getGlobal();
    data["company_id"] = company.unique_id;
    this.setState({
      isAddEditLoading: true
    })
    postAuth("/api/employee/new", data)
      .then((res) => {
        // console.log(res);
        const status = res.request.status;
        if (status === 201) {
          let employees = [...this.state.employees];
          employees.push(res.data.data);
          this.setState({ employees, isAddEditLoading: false, addEmp: {} }, () => {
            this.toggleModal("ADD");
            this.props.showAlert("Employee added successfully", "success");
          })
        } else {
          this.setState({
            isAddEditLoading: false
          })
          this.props.showAlert(res.response.data.message, 'error');
          // this.toggleModal("ADD");
          // console.log(res);
        }
      })

  }

  _submitEdit = () => {

    let data = { ...this.state.editEmp };
    this.setState({
      isAddEditLoading: true
    })
    authPatch(`/api/employee/${data.employee_id}`, data)
      .then((res) => {
        // console.log(res);
        // return
        const status = res.request.status;
        if (status === 200) {
          let employees = [...this.state.employees];
          let index = employees.findIndex((emp) => emp.employee_id === data.employee_id);
          employees[index] = { ...res.data.result };

          this.setState({ employees, isAddEditLoading: false }, () => {
            this.toggleModal("EDIT");
            this.props.showAlert("Employee edited successfully", "success");
          })
        } else {
          this.setState({
            isAddEditLoading: false
          })
          this.props.showAlert("Something went wrong, Please try again!", "error");
          this.toggleModal("EDIT");
          // console.log(res);
        }
      })

  }

  /**
   * @description delete employee http call
   */
  deleteEmployee = () => {
    // return;
    deleteAuth(`/api/employee/${this.state.deleteEmpId}`)
      .then((res) => {
        console.log(res);
        const status = res.request.status;
        if (status === 200) {
          let employees = [...this.state.employees]
          const index = employees.findIndex((emp) => emp._id === this.state.deleteEmpId)
          employees.splice(index, 1);
          this.setState({
            employees
          }, () => {
            this.toggleModal("DELETE");
            this.props.showAlert("Employee deleted", "success");
          })

        } else {
          this.props.showAlert("Something went wrong, Pleasse try again!", "error");
          this.toggleModal("DELETE");
        }

      })
    // http://3.16.48.200:3000/api/employee/5ec3c69ea6cb701722911628
  }

  _submitPhotos = () => {
    if (this.state.prifilePhoto !== '') {
      let formData = new FormData();
      formData.append('photo', this.state.prifilePhoto);
      this.setState({
        isAddEditLoading: true
      })
      axiosPatchFormData(`/api/employee/upload-photo/${this.state.editEmp.employee_id}`, formData)
        .then((res) => {
          // console.log(res);
          this.setState({
            isAddEditLoading: false
          })
          const status = res.request.status;
          if (status === 200) {
            this.props.showAlert("Profile picture uploaded", "success");
            let employees = [...this.state.employees];
            const index = employees.findIndex((emp) => emp.employee_id === this.state.editEmp.employee_id);
            if (employees[index].photos.length > 0) {
              employees[index].photos[0] = URL.createObjectURL(this.state.prifilePhoto)
            }
            else {
              employees[index].photos.push(URL.createObjectURL(this.state.prifilePhoto))
            }
            this.setState({
              prifilePhoto: '',
              editEmp: {}
            })
            this.toggleModal("FACE");
          }
          else {
            this.props.showAlert("Something went wrong, Pleasse try again!", "error");
            this.toggleModal("FACE");
          }
        })
    }
    else {
      this.props.showAlert("Pleasse select profile photo!", "error");
    }
  }

  /**
   * @description handhe searck key input
   * @param {string} event
   */
  searchSpace = (event: any) => {
    let keyword = event.target.value;
    this.setState({ search: keyword })
  }

  render() {


    // filter employees
    const items = this.state.employees.filter((data: any) => {
      if (this.state.search == null)
        return data
      else if (
        data.employee_id.toLowerCase().includes(this.state.search.toLowerCase()) ||
        data.name.toLowerCase().includes(this.state.search.toLowerCase())
      ) {
        return data
      }
    })

    // Logic for displaying todos
    const indexOfLastTodo = this.state.currentPage * this.state.itemsCountPerPage;
    const indexOfFirstTodo = indexOfLastTodo - this.state.itemsCountPerPage;
    const currentTodos = items.slice(indexOfFirstTodo, indexOfLastTodo);

    return (
      <>
        <Helmet>
          <title>Contatrack | Dashboard</title>
        </Helmet>

        <div data-aos="fade-down" className="innerwrapperbody">
          {/* <div className="labelzoneselect">
            <select>
              <option>Lavel 2 Zone B</option>
            </select>
          </div> */}

          <div className="employee-directoryprt margin20">
            <div className="topemployeeheader">
              <div className="leftheadingprt">
                <h2>EMPLOYEE DIRECTORY</h2>
                <input type="search" placeholder="Search" onChange={(e) => this.searchSpace(e)} />
              </div>
              <button
                className="borderbtn"
                onClick={() => this.toggleModal("ADD")}
              >
                + ADD EMPLOYEE
              </button>
            </div>
            <div className="tableprt">
              <table cellPadding="0" cellSpacing="0">
                <thead>
                  <tr>
                    <th>&nbsp;</th>
                    <th>ID NO.</th>
                    <th>NAME</th>
                    <th>Designation</th>
                    <th>DEPARTMENT</th>
                    <th>BREACHING</th>
                    <th>COVID-19 STATUS</th>
                    <th>&nbsp;</th>
                  </tr>
                </thead>

                <tbody>
                  {
                    this.state.isLoading ?
                      <tr>
                        <td colSpan={8}>
                          <p style={{ textAlign: 'center' }}>
                            <CircularProgress color="primary" />
                          </p>
                        </td>
                      </tr>
                      :
                      currentTodos.map((emp: any, i: any) => {
                        return (
                          <tr key={i}>
                            <td>
                              <button
                                className="userimg-btn"
                                onClick={() => {
                                  this.setState({ editEmp: emp });
                                  this.toggleModal("FACE")
                                }}
                              >
                                <div className="userimg">
                                  {
                                    emp.photos.length === 0
                                      ?
                                      emp.gender === 'Female'
                                        ?
                                        <img src={`${process.env.PUBLIC_URL}/assets/images/femaleProfileNoImage.jpeg`} />
                                        :
                                        <img src={`${process.env.PUBLIC_URL}/assets/images/menProfileNoImage.png`} />
                                      :
                                      <img src={emp.photos[emp.photos.length - 1]} />
                                  }

                                </div>
                              </button>
                            </td>
                            <td title="ID NO.">{emp.employee_id}</td>
                            <td title="NAME">
                              <b>{emp.name}</b>
                            </td>
                            <td title="Designation">{emp.designation || 'N/A'}</td>
                            <td title="DEPARTMENT">{emp.department || 'N/A'}</td>
                            <td>
                              <a href="">VIEW</a>
                            </td>
                            <td>
                              <a href="" style={{ color: emp.is_corona_positive ? 'red' : '#3ED2A0' }}>
                                {
                                  emp.is_corona_positive
                                    ?

                                    'POSITIVE'
                                    :
                                    `NAGETIVE`
                                }
                              </a><br />
                              {
                                emp.is_corona_positive
                                  ?

                                  <span>on {new Date(emp.corona_positive_date).getDate() + '/' + (new Date(emp.corona_positive_date).getMonth() + 1) + '/' + new Date(emp.corona_positive_date).getFullYear()}</span>
                                  :
                                  null
                              }

                            </td>
                            <td>
                              <div className="twobtn">
                                <button
                                  data-toggle="modal"
                                  onClick={() => {
                                    this.setState({ deleteEmpId: emp._id, deleteEmpName: emp.name }, () => {
                                      this.toggleModal("DELETE")
                                    })
                                  }}
                                >
                                  <img
                                    src={`${process.env.PUBLIC_URL}/assets/images/delete.svg`}
                                  />
                                </button>
                                <button
                                  onClick={() => {
                                    this.setState({ editEmp: emp });
                                    this.toggleModal("EDIT");
                                  }}
                                >
                                  <img
                                    src={`${process.env.PUBLIC_URL}/assets/images/edit.svg`}
                                  />
                                </button>
                              </div>
                            </td>
                          </tr>
                        );
                      })
                  }

                </tbody>
              </table>
            </div>
            <div className="pegignation-part">
              <Pagination
                activePage={this.state.currentPage}
                itemsCountPerPage={this.state.itemsCountPerPage}
                totalItemsCount={items.length}
                pageRangeDisplayed={5}
                itemClass="pegignation"
                innerClass="pegignation-wrapper"
                onChange={(page) => this.setState({ currentPage: page })}
              />
            </div>
          </div>
        </div>

        {/* add popup */}
        <Dialog
          open={this.state.isAddModalopen}
          onClose={() => { this.toggleModal("ADD") }}
          aria-labelledby="responsive-dialog-title"
        >
          <DialogTitle id="responsive-dialog-title">
            <div className="dialog-header">
              <h4 className="modal-title">Add Employee</h4>
              <button
                type="button"
                className="close"
                onClick={() => { this.toggleModal("ADD") }}
              >
                &times;
              </button>
            </div>
          </DialogTitle>
          <DialogContent>
            <div className="row">
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Full Name</label>
                  <input
                    type="text"
                    defaultValue={this.state.addEmp.name}
                    onChange={(e) => this.handleAddChange(e, "name")}
                    onBlur={(e) => this.validateInput(e, "name")}

                  />
                  <span className="error">{this.state.validation.name}</span>
                </div>
              </div>
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Email</label>
                  <input
                    type="text"
                    defaultValue={this.state.addEmp.email}
                    onChange={(e) => this.handleAddChange(e, "email")}
                    onBlur={(e) => this.validateInput(e, "email")}
                  />
                  <span className="error">{this.state.validation.email}</span>
                </div>
              </div>
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Password</label>
                  <input
                    type="text"
                    defaultValue={this.state.addEmp.password}
                    onChange={(e) => this.handleAddChange(e, "password")}
                    onBlur={(e) => this.validateInput(e, "password")}
                  />
                  <span className="error">
                    {this.state.validation.password}
                  </span>
                </div>
              </div>
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Employee ID</label>
                  <input
                    type="text"
                    defaultValue={this.state.addEmp.employee_id}
                    onChange={(e) => this.handleAddChange(e, "employee_id")}
                    onBlur={(e) => this.validateInput(e, "employee_id")}
                  />
                  <span className="error">
                    {this.state.validation.employee_id}
                  </span>
                </div>
              </div>
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Designation (Optional)</label>
                  <input
                    type="text"
                    defaultValue={this.state.addEmp.designation}
                    onChange={(e) => this.handleAddChange(e, "designation")}
                  />
                </div>
              </div>
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Department (Optional)</label>
                  <input
                    type="text"
                    defaultValue={this.state.addEmp.department}
                    onChange={(e) => this.handleAddChange(e, "department")}
                  />
                </div>
              </div>
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Phone</label>
                  <input
                    type="text"
                    maxLength={10}
                    defaultValue={this.state.addEmp.phone}
                    onChange={(e) => this.handleAddChange(e, "phone")}
                    onBlur={(e) => this.validateInput(e, "phone")}
                  />
                  <span className="error">{this.state.validation.phone}</span>
                </div>
              </div>

              <div className="col-md-6">
                <div className="modal-input">
                  <label>Date of Birth</label>
                  {/* <input type="text" /> */}
                  <DatePicker
                    placeholderText="mm/dd/yyyy"
                    selected={this.state.toDay}
                    onChange={(date) => this.onChangeDate(date)}
                    maxDate={new Date()}
                  />
                </div>
              </div>
              <div className="col-md-6">
                <div className="modal-input">
                  <label>Gender</label>
                  <select onChange={(e) => this.handleAddChange(e, "gender")}>
                    <option>Male</option>
                    <option>Female</option>
                  </select>
                </div>
              </div>

              <div className="col-md-12">
                <div className="modal-input">
                  <label>COVID-19 Status</label>
                  <div className="planprt">
                    <div className="plan-check">
                      <input type="radio" name="renewType" onChange={(e) => this.handleAddChange(e, 'is_corona_positive')} value={'positive'} checked={this.state.addEmp.is_corona_positive} id="positive" />
                      <label htmlFor="positive" >Positive </label>
                    </div>
                    <div className="plan-check">
                      <input type="radio" name="renewType" onChange={(e) => this.handleAddChange(e, 'is_corona_positive')} value={'nagetive'} checked={!this.state.addEmp.is_corona_positive} id="nagetive" />
                      <label htmlFor="nagetive" >Negative</label>
                    </div>
                  </div>
                </div>
              </div>
              {
                this.state.addEmp.is_corona_positive
                  ?
                  <div className="col-md-6">
                    <div className="modal-input">
                      <label>COVID-19 Positive date</label>
                      {/* <input type="text" /> */}
                      <DatePicker
                        placeholderText="mm/dd/yyyy"
                        selected={new Date(this.state.addEmp.corona_positive_date) || new Date()}
                        onChange={(date) => this.onChangeCoronaDate(date)}
                        maxDate={new Date()}
                      />
                    </div>
                  </div>
                  :
                  null
              }


            </div>
          </DialogContent>
          <DialogActions>
            <button
              type="button"
              className="canbtn"
              onClick={() => { this.toggleModal("ADD") }}
            >
              Cancel
            </button>
            <button
              type="button"
              className="addbtn"
              disabled={
                this.state.isAddEditLoading ||

                this.state.addEmp.name === '' ||
                this.state.addEmp.email === '' ||
                this.state.addEmp.password === '' ||
                this.state.addEmp.phone === '' ||
                this.state.addEmp.employee_id === '' ||

                this.state.validation.name !== '' ||
                this.state.validation.email !== '' ||
                this.state.validation.password !== '' ||
                this.state.validation.phone !== '' ||
                this.state.validation.employee_id !== ''
              }
              onClick={(e) => {
                // this.toggleModal("ADD");

                this._submitAdd(e);
              }}
            >
              {
                this.state.isAddEditLoading
                  ?
                  <CircularProgress color="inherit" size={25} />
                  :
                  'Add'
              }
            </button>
          </DialogActions>
        </Dialog>

        {/* edit popup */}
        <Dialog
          open={this.state.isEditModalopen}
          onClose={() => this.toggleModal("EDIT")}
          aria-labelledby="responsive-dialog-title"
        >
          <DialogTitle id="responsive-dialog-title">
            <div className="dialog-header">
              <h4 className="modal-title">Edit Employee</h4>
              <button
                type="button"
                className="close"
                onClick={() => this.toggleModal("EDIT")}
              >
                &times;
              </button>
            </div>
          </DialogTitle>
          <DialogContent>
            <div className="row">
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Full Name</label>
                  <input type="text"
                    defaultValue={this.state.editEmp.name}
                    onChange={(e) => this.handleEditChange(e, "name")}
                    onBlur={(e) => this.validateInput(e, "name")}
                  />
                  <span className="error">{this.state.validation.name}</span>
                </div>
              </div>
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Email</label>
                  <input type="text"
                    defaultValue={this.state.editEmp.email}
                    onChange={(e) => this.handleEditChange(e, "email")}
                    onBlur={(e) => this.validateInput(e, "email")}
                  />
                  <span className="error">{this.state.validation.email}</span>
                </div>
              </div>
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Phone</label>
                  <input type="text"
                    maxLength={10}
                    defaultValue={this.state.editEmp.phone}
                    onChange={(e) => this.handleEditChange(e, "phone")}
                    onBlur={(e) => this.validateInput(e, "phone")}
                  />
                  <span className="error">{this.state.validation.phone}</span>
                </div>
              </div>

              <div className="col-md-12">
                <div className="modal-input">
                  <label>Designation (Optional)</label>
                  <input type="text"
                    defaultValue={this.state.editEmp.designation}
                    onChange={(e) => this.handleEditChange(e, "designation")}
                  />
                  <span className="error">{this.state.validation.designation}</span>
                </div>
              </div>
              <div className="col-md-12">
                <div className="modal-input">
                  <label>Department (Optional)</label>
                  <input type="text"
                    defaultValue={this.state.editEmp.department}
                    onChange={(e) => this.handleEditChange(e, "department")}
                  />
                  <span className="error">{this.state.validation.department}</span>
                </div>
              </div>

              <div className="col-md-6">
                <div className="modal-input">
                  <label>Date of Birth</label>
                  {/* <input type="text" /> */}
                  <DatePicker
                    placeholderText="mm/dd/yyyy"
                    selected={new Date(this.state.editEmp.dob) || new Date()}
                    onChange={(date) => this.onChangeDateEditDOB(date)}
                    maxDate={new Date()}
                  />
                </div>
              </div>
              <div className="col-md-6">
                <div className="modal-input">
                  <label>Gender</label>
                  <select
                    onChange={(e) => this.handleEditChange(e, "gender")}
                    defaultValue={this.state.editEmp.gender}>
                    <option>Male</option>
                    <option>Female</option>
                  </select>
                </div>
              </div>
              <div className="col-md-12">
                <div className="modal-input">
                  <label>COVID-19 Status</label>
                  <div className="planprt">
                    <div className="plan-check">
                      <input type="radio" name="renewType" onChange={(e) => this.handleEditChange(e, 'is_corona_positive')} value={'positive'} checked={this.state.editEmp.is_corona_positive} id="positive" />
                      <label htmlFor="positive" >Positive </label>
                    </div>
                    <div className="plan-check">
                      <input type="radio" name="renewType" onChange={(e) => this.handleEditChange(e, 'is_corona_positive')} value={'nagetive'} checked={!this.state.editEmp.is_corona_positive} id="nagetive" />
                      <label htmlFor="nagetive" >Negative</label>
                    </div>
                  </div>
                </div>
              </div>
              {
                this.state.editEmp.is_corona_positive
                  ?
                  <div className="col-md-6">
                    <div className="modal-input">
                      <label>COVID-19 Positive date</label>
                      {/* <input type="text" /> */}
                      <DatePicker
                        placeholderText="mm/dd/yyyy"
                        selected={new Date(this.state.editEmp.corona_positive_date) || new Date()}
                        onChange={(date) => this.onChangeCoronaDateEdit(date)}
                        maxDate={new Date()}
                      />
                    </div>
                  </div>
                  :
                  null
              }
            </div>
          </DialogContent>
          <DialogActions>
            <button
              type="button"
              className="canbtn"
              onClick={() => this.toggleModal("EDIT")}
            >
              Cancel
            </button>
            <button
              type="button"
              className="addbtn"
              disabled={
                this.state.isAddEditLoading ||

                this.state.editEmp.name === '' ||
                this.state.editEmp.email === '' ||
                this.state.editEmp.password === '' ||
                this.state.editEmp.phone === '' ||

                this.state.validation.name !== '' ||
                this.state.validation.email !== '' ||
                this.state.validation.password !== '' ||
                this.state.validation.phone !== ''}
              onClick={() => this._submitEdit()}
            >
              {
                this.state.isAddEditLoading
                  ?
                  <CircularProgress color="inherit" size={25} />
                  :
                  'UPDATE'
              }
            </button>
          </DialogActions>
        </Dialog>

        {/* face image upload popup */}
        <Dialog
          open={this.state.isFaceImgOpen}
          onClose={() => this.toggleModal("FACE")}
          aria-labelledby="responsive-dialog-title"
        >
          <DialogTitle id="responsive-dialog-title">
            <div className="dialog-header">
              <h4 className="modal-title">Add Image</h4>
              <button
                type="button"
                className="close"
                onClick={() => this.toggleModal("FACE")}
              >
                &times;
              </button>
            </div>
          </DialogTitle>
          <DialogContent>
            <div className="row">
              <div className="col-md-12">
                <label className="upload-heading">Profile</label>
                <div className="uploadsection">
                  <div className="leftprof">
                    <img
                      src={`${process.env.PUBLIC_URL}/assets/images/upload-icon.png`}
                    />
                    Profile Pic
                  </div>
                  <div className="browseBtnprt">
                    <input
                      type="file"
                      onChange={(event: any) => {
                        // console.log(event.target.value)
                        // let data = { ...this.state.editRowData }
                        // data.url = URL.createObjectURL(event.target.files[0])
                        this.setState({ prifilePhoto: event.target.files[0] })
                      }}
                    />
                    <span>Upload</span>
                  </div>
                </div>
              </div>

              <div className="col-md-12 margin30 profilepopprt">
                <div className="rightquestionicon">
                  <img
                    src={`${process.env.PUBLIC_URL}/assets/images/question-mark.png`}
                  />
                  <div className="imgmoodshowprt text-center">
                    <p>
                      Take photo of the front at different angle shown below{" "}
                    </p>
                    <ul>
                      <li>
                        <img
                          src={`${process.env.PUBLIC_URL}/assets/images/font-face.png`}
                        />
                        <p>Front</p>
                      </li>
                      <li>
                        <img
                          src={`${process.env.PUBLIC_URL}/assets/images/left-face.png`}
                        />
                        <p>Left</p>
                      </li>
                      <li>
                        <img
                          src={`${process.env.PUBLIC_URL}/assets/images/right-face.png`}
                        />
                        <p>Right</p>
                      </li>
                    </ul>
                  </div>
                </div>
                <label className="upload-heading">
                  Facial Recognition Photo
                </label>
                <div className="uploadsection">
                  <div className="leftprof">
                    <img
                      src={`${process.env.PUBLIC_URL}/assets/images/upload-icon.png`}
                    />
                    Front Face
                  </div>
                  <div className="browseBtnprt">
                    <input type="file" />
                    <span>Upload</span>
                  </div>
                </div>
                <div className="uploadsection">
                  <div className="leftprof">
                    <img
                      src={`${process.env.PUBLIC_URL}/assets/images/upload-icon.png`}
                    />
                    Left side of the face
                  </div>
                  <div className="browseBtnprt">
                    <input type="file" />
                    <span>Upload</span>
                  </div>
                </div>
                <div className="uploadsection">
                  <div className="leftprof">
                    <img
                      src={`${process.env.PUBLIC_URL}/assets/images/upload-icon.png`}
                    />
                    Right side of the face
                  </div>
                  <div className="browseBtnprt">
                    <input type="file" />
                    <span>Upload</span>
                  </div>
                </div>
              </div>
            </div>
          </DialogContent>
          <DialogActions>
            <button
              type="button"
              className="addbtn updatebtn"
              disabled={this.state.isAddEditLoading}
              onClick={() => this._submitPhotos()}
            >

              {
                this.state.isAddEditLoading
                  ?
                  <CircularProgress color="inherit" size={25} />
                  :
                  'UPLOAD'
              }
            </button>
          </DialogActions>
        </Dialog>

        {/* delete dialog */}
        <Dialog
          open={this.state.isDeleteModalOpen}
          onClose={() => this.toggleModal("DELETE")}
          aria-labelledby="responsive-dialog-title"
        >
          <DialogContent>
            <div className="modal-body text-center">
              <div className="deletebody">
                <h4>Delete</h4>
                <p>
                  Are you sure you want to permanently remove {this.state.deleteEmpName} from employee list?
                </p>

                <div className="deletebtnprt">
                  <button
                    type="button"
                    className="canbtn"
                    onClick={() => this.toggleModal("DELETE")}
                  >
                    Cancel
                  </button>
                  <button
                    type="button"
                    className="delbtn"
                    onClick={() => {
                      this.deleteEmployee();
                    }}
                  >
                    Delete
                  </button>
                </div>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </>
    );
  }
}

export default HOC(Employee);

