import React, { Component, getGlobal  } from "reactn";
import { update } from '../../../util/Service';
import { Helmet } from "react-helmet";
import { HOC } from "../../../util/HOC";

type props = {
	history: any;
	showAlert: any
};
type states = {
  value: any;
  validation: any;
};

class ChangePass extends Component<props, states> {
  constructor(props: any) {
    super();
    this.state = {
      value: {
        changepass: "",
        changepassconfirm: "",
      },
      validation: {
        changepass: "",
        changepassconfirm: "",
      },
    };
  }

  handleChange = (event: any, name: string) => {
    let value = { ...this.state.value };
		value[name] = event.target.value;
		this.validateInput(event, name);
		this.setState({ value });
  };
  validateInput = (event: any, name: string) => {
    const value = event.target.value;
    let validation = { ...this.state.validation };
    if((this.state.value.changepass !== this.state.value.changepassconfirm) && this.state.value.changepassconfirm != ''){
			console.log('error')
		}
	};
	
	_submitForm = ()=>{
		let validation = { ...this.state.validation };
		if((this.state.value.changepass !== this.state.value.changepassconfirm)){
			validation['changepassconfirm'] = 'Both passwords should match';
			this.setState({validation})
		}else{
			validation['changepassconfirm'] = '';
			this.setState({validation});
		
			let {company} = getGlobal();
			let data = {
				email:company.email,
				password:this.state.value.changepass
			}
			update('/api/company/changepassword', data).then(response=>{
				if(response.success){
					this.props.showAlert('Password changed successfully', 'success');
					let value = { ...this.state.value };
					validation['changepass'] = '';
					validation['changepassconfirm'] = '';
					this.setState({value});
				}else{
					console.log(response)
					validation['changepassconfirm'] = 'Password character must be greater than 7';
					this.setState({validation});
				}
			})
			.catch(error=>{
				console.log(error)
			})
		}
	}
  render() {
    return (
      <>
        <Helmet>
          <title>Contatrack | Dashboard | Change Password</title>
        </Helmet>

        <div data-aos="zoom-in" className="innerwrapperbody">
          <div className="twopanel-signin">
            <div className="leftPrt">
              <div className="leftbox">
                <h2 className="text-center">Change Password</h2>

                <div className="input-panel margin30">
                  <label>New Password</label>
                  <input
                    type="password"
										name="changepass"
										value={this.state.value.changepass}
                    onChange={(e) => {
                      this.handleChange(e, "changepass");
                    }}
                  />
								
                </div>
                <div className="input-panel">
                  <label>Re-enter New Password</label>
                  <input
                    type="password"
										name="changepassconfirm"
										value={this.state.value.changepassconfirm}
                    onChange={(e) => {
                      this.handleChange(e, "changepassconfirm");
										}}
                  />
										<span className="error" >{this.state.validation.changepassconfirm}</span>
                </div>

                <button className="blue inner-button normabtn" onClick={()=>{
									this._submitForm()
								}}>Update</button>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default HOC(ChangePass);
