import React, { Component, getGlobal } from 'reactn';
import { Helmet } from "react-helmet";
import { HOC } from '../../../util/HOC';
import { get } from '../../../util/Service'
import CircularProgress from '@material-ui/core/CircularProgress/CircularProgress';
type props = {
	history: any;
};
type states = {
	cmpDetails: any;
	isLoading: boolean;
};

class DashLanding extends Component<props, states> {
	constructor() {
		super();
		this.state = {
			cmpDetails: {},
			isLoading: false
		}
	}

	componentDidMount() {
		const { company } = getGlobal();
		this.setState({
			isLoading: true
		})
		get(`/api/company/details/${'5ed14b780f98dd3308189ad6'}`)
			.then((res) => {
				console.log(res)
				this.setState({
					isLoading: false
				})
				const status = res.request.status;
				if (status === 201) {
					this.setState({
						cmpDetails: { ...res.data.data }
					})
				}
				else {

				}
			})
	}
	render() {
		const { current_plan_details } = this.state.cmpDetails;
		return (
			<>
				<Helmet>
					<title>Contatrack | Report</title>
				</Helmet>

				<div className="innerwrapperbody">
					<h2 className="upper-heading">Dashboard</h2>

					<div className="dashboardplantable margin20">

						<div className="tableprt">
							<table cellPadding="0" cellSpacing="0">
								<thead>
									<tr>

										<th>Current Plan</th>
										{/* <th>Billing Date</th> */}
										<th>Payment Method</th>
										<th>Location</th>


									</tr>
								</thead>

								<tbody>
									{
										this.state.isLoading
											?
											<tr>
												<td colSpan={3}>
													<p style={{ textAlign: 'center' }}>
														<CircularProgress color="primary" />
													</p>
												</td>
											</tr>
											:

											Object.keys(this.state.cmpDetails).length > 0
												?
												<>
													<tr>
														<td title="Current Plan">
															{current_plan_details.plan.plan_name} Plan
													</td>
														{/* <td title="Billing Date">
														<span>Cancels</span>
														18/4/2022
													</td> */}
													<td title="Payment Method">
														In Cash
													</td>
													<td title="Location">
														N/A
													</td>

													</tr>
												</>
												: null
									}

								</tbody>
							</table>
						</div>

						<div className="rightbuttonprt">
							<button className="renewbtn">RENEW</button>
							<button className="blue inner-button">Upgrade</button>
						</div>

					</div>

				</div>
			</>
		)
	}
}

export default HOC(DashLanding);